"use client";

import { memo } from "react";
import { Handle, Position, NodeProps, NodeResizer } from "@xyflow/react"; // Import NodeResizer
import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { useStore } from "@/lib/store";
import Editor from "@monaco-editor/react";

export const CustomNode = memo(({ id, data, selected }: any) => {
  const updateNodeCode = useStore((state: any) => state.updateNodeCode);

  return (
    // Note: We removed the fixed w-[450px] so it can resize freely
    <div className="relative h-full w-full min-w-[300px] min-h-[200px]">
      <NodeResizer 
        color="#3b82f6" 
        isVisible={selected} 
        minWidth={300} 
        minHeight={200} 
      />
      
      <Card className="h-full w-full border-2 border-border shadow-lg bg-card overflow-hidden flex flex-col">
        <Handle type="target" position={Position.Top} className="w-3 h-3 !bg-primary" />

        <CardHeader className="p-3 border-b border-border bg-secondary/30 shrink-0">
          <h3 className="font-semibold text-sm">{data.label}</h3>
        </CardHeader>

        <CardContent className="p-0 flex-1 flex flex-col min-h-0">
          <Tabs defaultValue={data.files?.[0]?.name} className="w-full flex-1 flex flex-col">
            <TabsList className="w-full justify-start rounded-none border-b border-border h-9 shrink-0">
              {data.files?.map((file: any) => (
                <TabsTrigger key={file.name} value={file.name} className="text-xs">
                  {file.name}
                </TabsTrigger>
              ))}
            </TabsList>

            {data.files?.map((file: any, index: number) => (
              <TabsContent key={file.name} value={file.name} className="m-0 p-0 flex-1">
                <Editor
                  height="100%" // Editor now fills the resized height!
                  theme="vs-dark"
                  defaultLanguage="typescript"
                  value={file.content}
                  onChange={(val) => updateNodeCode(id, index, val || "")}
                  options={{
                    minimap: { enabled: false },
                    fontSize: 12,
                    scrollBeyondLastLine: false,
                    automaticLayout: true, // This is crucial for resizing
                  }}
                />
              </TabsContent>
            ))}
          </Tabs>
        </CardContent>

        <Handle type="source" position={Position.Bottom} className="w-3 h-3 !bg-primary" />
      </Card>
    </div>
  );
});